import { FunctionComponent, useMemo } from "react";
import CSS, { Property } from "csstype";
import styles from "./Pilldefault.module.css";

type PilldefaultType = {
  beds?: string;

  /** Style props */
  pilldefaultPilldefaultPosition?: Property.Position;
  pilldefaultPilldefaultWidth?: Property.Width;
  pilldefaultPilldefaultBackgroundColor?: Property.BackgroundColor;
  bedsColor?: Property.Color;
};

const Pilldefault: FunctionComponent<PilldefaultType> = ({
  beds,
  pilldefaultPilldefaultPosition,
  pilldefaultPilldefaultWidth,
  pilldefaultPilldefaultBackgroundColor,
  bedsColor,
}) => {
  const pilldefaultStyle: CSS.Properties = useMemo(() => {
    return {
      position: pilldefaultPilldefaultPosition,
      width: pilldefaultPilldefaultWidth,
      backgroundColor: pilldefaultPilldefaultBackgroundColor,
    };
  }, [
    pilldefaultPilldefaultPosition,
    pilldefaultPilldefaultWidth,
    pilldefaultPilldefaultBackgroundColor,
  ]);

  const bedsStyle: CSS.Properties = useMemo(() => {
    return {
      color: bedsColor,
    };
  }, [bedsColor]);

  return (
    <div className={styles.pilldefault} style={pilldefaultStyle}>
      <div className={styles.beds} style={bedsStyle}>
        {beds}
      </div>
    </div>
  );
};

export default Pilldefault;
